import useGlobalStore from '@/store/common/global'
import { AccountSwitch, AccountSwitchDrawer, CetusTooltip } from '@cetus/design'
import RecentTransactions from '@cetus/design/src/components/wallet/RecentTransactions'
import { useAccountServiceName } from '@cetus/hooks'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Icon, RefreshButton } from '@cetus/ui-kit'
import { addressAbridge } from '@cetus/utils'
import { Center, HStack, Image, Text, useDisclosure, VStack } from '@chakra-ui/react'
import { useCallback, useState } from 'react'
import { labelText } from './H5Main'

type ProfileHeaderProps = {
  handleRefresh: (isManual: boolean) => void
  totalValue: string
  isLoading: boolean
}

function ProfileHeader({ handleRefresh, totalValue, isLoading }: ProfileHeaderProps) {
  const { isApp } = useWindowWidth()
  const { currentAccount, accounts, accountServiceNameObj } = useAccountStore()
  const { isShowProfileAssets, setIsShowProfileAssets } = useGlobalStore()
  const [isOpenRecentTrans, setIsOpenRecentTrans] = useState<boolean>(false)
  const { onClose } = useDisclosure()

  const { isOpen, onOpen, onClose: onAccountClose, onToggle } = useDisclosure()
  const { getServiceNames } = useAccountServiceName()
  const handleClick = useCallback(() => {
    if (!isOpen) {
      getServiceNames(accounts)
    }
    onToggle()
  }, [isOpen, accounts])
  return (
    <VStack w="100%" align="flex-start" gap="12px">
      <HStack w="100%" justify="space-between">
        <HStack gap="12px">
          <Image src="/images/img_profile@2x.png" w="72px" h="72px" />
          {isApp ? (
            <H5Content
              isApp={isApp}
              isShowProfileAssets={isShowProfileAssets}
              currentAccount={currentAccount}
              handleClick={handleClick}
              accountServiceNameObj={accountServiceNameObj}
              changeProfileAssets={() => setIsShowProfileAssets(!isShowProfileAssets)}
              changeOpenRecentTrans={() => setIsOpenRecentTrans(!isOpenRecentTrans)}
            />
          ) : (
            <AccountSwitch currentAccount={currentAccount} accounts={accounts} onClose={onClose} isProfile={true} />
          )}
        </HStack>
        <HStack>
          {!isApp && (
            <HStack
              p="6px 5px 6px 3px"
              h="32px"
              justify="center"
              align="center"
              bg="block_color_opacity.50"
              backdropFilter="blur(20px)"
              borderRadius="8px"
              border="1px solid"
              borderColor="border"
              cursor="pointer"
              gap="2px"
              _hover={{
                // bg: 'button_outline_hov_bg',
                svg: {
                  fill: 'text_caption'
                },
                p: {
                  color: 'text_caption'
                }
              }}
              onClick={() => {
                setIsOpenRecentTrans(true)
              }}
            >
              <Icon xlinkHref="#icon-transactions" svgW="20px" svgH="20px" />
              <Text>Transactions</Text>
            </HStack>
          )}
          {!isApp && (
            <HStack
              p="6px 5px"
              h="32px"
              justify="center"
              align="center"
              bg="block_color_opacity.50"
              backdropFilter="blur(20px)"
              borderRadius="8px"
              border="1px solid"
              borderColor="border"
              cursor="pointer"
              gap="2px"
              _hover={{
                // bg: 'button_outline_hov_bg',
                svg: {
                  fill: 'text_caption'
                }
              }}
              onClick={() => setIsShowProfileAssets(!isShowProfileAssets)}
            >
              <CetusTooltip
                showTooltip={isApp ? false : true}
                placement="top"
                tooltip={<Text fontSize="12px">{isShowProfileAssets ? 'Hide Value' : 'Show Value'}</Text>}
              >
                <Center>
                  <Icon xlinkHref={isShowProfileAssets ? '#icon-hide_eyes' : '#icon-show_eyes'} />
                </Center>
              </CetusTooltip>
            </HStack>
          )}
          <RefreshButton
            handleRefresh={handleRefresh}
            bg="block_color_opacity.50"
            backdropFilter="blur(20px)"
            w="32px"
            h="32px"
            isAutoRefresh={true}
            refreshInterval={30}
            innerStyle={{ bg: 'transparent' }}
          />
        </HStack>
      </HStack>
      {/* <HStack w="100%" justify="space-between" mt="2px">
        <Skeleton isLoaded={!isLoading} h="22px">
          <Text mt="-6px" letterSpacing="1px" fontSize="32px" lineHeight="32px" color="text_caption" fontWeight="500">
            {isShowAssets(formatCurrency(totalValue, 2), isShowProfileAssets)}
          </Text>
        </Skeleton>
      </HStack> */}
      {isApp && <AccountSwitchDrawer isOpen={isOpen} onClose={onAccountClose} />}
      {isOpenRecentTrans && <RecentTransactions isOpen={isOpenRecentTrans} onClose={() => setIsOpenRecentTrans(false)} />}
    </VStack>
  )
}
const H5Content = ({
  isApp,
  isShowProfileAssets,
  currentAccount,
  handleClick,
  accountServiceNameObj,
  changeProfileAssets,
  changeOpenRecentTrans
}: {
  isApp: boolean
  isShowProfileAssets: boolean
  currentAccount: any
  handleClick: () => void
  changeProfileAssets: () => void
  changeOpenRecentTrans: () => void
  accountServiceNameObj: any
}) => {
  return (
    <VStack align="flex-start">
      <HStack>
        <HStack gap="4px" cursor="pointer" onClick={handleClick}>
          <VStack align="flex-start">
            {currentAccount?.label && (
              <Text fontSize="14px" lineHeight="14px">
                {labelText(currentAccount?.label)}
              </Text>
            )}
            <Text color="text_caption" fontSize="16px" fontWeight="500" lineHeight="14px">
              {accountServiceNameObj?.[currentAccount?.address] || addressAbridge(currentAccount?.address)}
            </Text>
          </VStack>
          <Icon
            xlinkHref="#icon-icon_descending_nor"
            svgFill="text_caption"
            svgW="20px"
            svgH="20px"
            transform={'rotate(0deg)'}
            transition="transform 0.3s"
          />
        </HStack>
        <CetusTooltip
          showTooltip={isApp ? false : true}
          placement="bottom-end"
          tooltip={<Text fontSize="12px">{isShowProfileAssets ? 'Hide Value' : 'Show Value'}</Text>}
        >
          <Center>
            <Icon
              xlinkHref={isShowProfileAssets ? '#icon-hide_eyes' : '#icon-show_eyes'}
              svgFill={isShowProfileAssets ? 'text_caption' : 'text_caption'}
              // svgHover={isShowProfileAssets ? 'primary' : isApp ? 'text_paragraph' : 'text_caption'}
              onClick={changeProfileAssets}
            />
          </Center>
        </CetusTooltip>
      </HStack>
      <HStack h="16px" align="center" gap="0" cursor="pointer" onClick={changeOpenRecentTrans}>
        <Text h="16px" lineHeight="16px">
          Transactions
        </Text>
        <Icon xlinkHref="#icon-icon_ascending" fontSize="16px" transform="rotate(90deg)" />
      </HStack>
    </VStack>
  )
}
export default ProfileHeader
