import { Block } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { HStack, Switch, Text } from '@chakra-ui/react'
import SearchAssetsInput from './SearchAssetsInput'
type FilterAssetsProps = {
  inputValue: string
  changeInputValue: (value: string) => void
  isShowUnknownCoin: boolean
  handleIsShowUnknownCoin: (event: React.ChangeEvent<HTMLInputElement> | boolean) => void
  isHideLowAsset: boolean
  handleIsHideLowAsset: (event: React.ChangeEvent<HTMLInputElement> | boolean) => void
  unknownCoinCount: number
}
function FilterAssets({
  inputValue,
  changeInputValue,
  isShowUnknownCoin,
  handleIsShowUnknownCoin,
  isHideLowAsset,
  handleIsHideLowAsset,
  unknownCoinCount
}: FilterAssetsProps) {
  const { isApp, windowWidth } = useWindowWidth()
  return (
    <HStack w={{ base: '100%', lg: 'unset' }} gap="8px" flexDirection={{ base: 'column', lg: 'row' }}>
      <SearchAssetsInput inputValue={inputValue} changeInputValue={changeInputValue} />
      <HStack w={{ base: '100%', lg: 'unset' }} justify="space-between">
        <Block
          w={{ base: '50%', lg: 'unset' }}
          borderRadius="8px"
          p={{ base: '0 4px', lg: '0px 8px' }}
          h="32px"
          onClick={isApp ? () => handleIsShowUnknownCoin(!isShowUnknownCoin) : () => {}}
          bg={isApp ? (isShowUnknownCoin ? 'card_bg' : 'bg_secondary') : 'background'}
        >
          <HStack h="100%" justify={{ base: 'center', lg: 'space-between' }}>
            <Text whiteSpace="nowrap" fontSize="12px" color={isApp && isShowUnknownCoin ? 'primary' : 'text_caption'}>
              Show Unknown Coin ({unknownCoinCount})
            </Text>
            {!isApp && <Switch isChecked={isShowUnknownCoin} onChange={handleIsShowUnknownCoin} />}
          </HStack>
        </Block>
        <Block
          w={{ base: '50%', lg: 'unset' }}
          borderRadius="8px"
          p="0px 8px"
          h="32px"
          onClick={isApp ? () => handleIsHideLowAsset(!isHideLowAsset) : () => {}}
          bg={isApp ? (isHideLowAsset ? 'card_bg' : 'bg_secondary') : 'background'}
        >
          <HStack h="100%" justify={{ base: 'center', lg: 'space-between' }}>
            <Text whiteSpace="nowrap" fontSize="12px" color={isApp && isHideLowAsset ? 'primary' : 'text_caption'}>
              Hide Low Asset
            </Text>
            {!isApp && <Switch isChecked={isHideLowAsset} onChange={handleIsHideLowAsset} />}
          </HStack>
        </Block>
      </HStack>
    </HStack>
  )
}

export default FilterAssets
