import useVaultList from '@/hooks/vault-v2/useVaultList'
import usePositionStore from '@/store/position'
import useVaultsPositionStore from '@/store/vaults-v2/useVaultsPosition'
import { Block } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { fixCoinType } from '@cetusprotocol/common-sdk'
import { Box, HStack, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import PoolsFilter from '../../pools/PoolsFilter'
import ProfileMenus from '../ProfileMenus'
import ProfilePosition from './ProfilePosition'
import ProfileVaults from './ProfileVaults'

function ProfileLiquidity() {
  const { currentAccount } = useAccountStore()
  const { vaultsPositionObj } = useVaultsPositionStore()
  const { showPosListLength, posBaseListLoading, setShowPosListLength, posBaseListGroupByPool } = usePositionStore()
  const { getCurrentTabNum } = useVaultList()

  const [selectCoinList, setSelectCoinList] = useState<Token[]>([])
  const onClickSelectCoinList = (tokenInfo: Token) => {
    console.log('🚀 ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => [...prev, tokenInfo])
  }
  const onDeleteSelectCoinList = (tokenInfo: Token) => {
    console.log('🚀 ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => prev.filter(ele => ele?.coin_type !== tokenInfo?.coin_type))
  }

  useEffect(() => {
    if (!currentAccount?.address) {
      setShowPosListLength('')
    }
  }, [currentAccount?.address])

  useEffect(() => {
    // 组件卸载时 列表条数重置
    return () => {
      setShowPosListLength('')
    }
  }, [])

  const [currentTab, setCurrentTab] = useState('positions')

  const queryOptions: any = {
    sortRule: 'desc',
    sortType: 'tvl'
  }
  const isYourHoldings = true
  const tabList = useMemo(() => {
    const filterTokens = currentTab == 'positions' ? [] : selectCoinList
    return [
      {
        label: 'Positions',
        value: 'positions',
        num: !posBaseListLoading ? Number(showPosListLength) : ''
      },
      {
        label: 'Vaults',
        value: 'vaults',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'all', isYourHoldings, selectCoinList: filterTokens }) || ''
      }
    ]
  }, [posBaseListLoading, showPosListLength, selectCoinList, vaultsPositionObj])

  const [showPosListGroupByPool, setShowPosListGroupByPool] = useState<any>([])

  useEffect(() => {
    const filterTokens = currentTab == 'vaults' ? [] : selectCoinList
    const firstTokenAddress = filterTokens[0]?.coin_type ? fixCoinType(filterTokens[0]?.coin_type) : undefined
    const lastTokenAddress = filterTokens[1]?.coin_type ? fixCoinType(filterTokens[1]?.coin_type) : undefined
    const filterArr = Object.values(posBaseListGroupByPool).filter((filterItem: any) => {
      if (firstTokenAddress && lastTokenAddress) {
        return (
          (fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == lastTokenAddress) ||
          (fixCoinType(filterItem.tokenA.coin_type) == lastTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress)
        )
      } else if (firstTokenAddress) {
        return fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress || fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress
      } else {
        return filterItem
      }
    })
    console.log('🚀 ~ useEffect ~ filterArr:', Object.values(posBaseListGroupByPool), filterArr)
    setShowPosListGroupByPool(filterArr)
    const posListLength = filterArr.reduce((sum, item: any) => {
      return sum + (item.list ? item.list.length : 0)
    }, 0)
    if (currentAccount?.address) {
      setShowPosListLength(posListLength.toString())
    }
  }, [selectCoinList, posBaseListGroupByPool])
  const { isApp } = useWindowWidth()
  return (
    <Block
      borderRadius="16px"
      p={{ base: '0', lg: '0px 16px 16px' }}
      bg={{ base: 'none', lg: 'bg_primary_opacity.90' }}
      backdropFilter={{ base: 'none', lg: 'blur(20px)' }}
      border="none"
    >
      <VStack gap={{ base: '8px', lg: '16px' }} w="100%">
        <HStack w="100%" gap="12px" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }}>
          <ProfileMenus
            type="tab"
            currentTab={currentTab}
            tabs={tabList}
            menuHeight={isApp ? '20px' : '60px'}
            haveActiveLine={false}
            onTabChange={tab => {
              setSelectCoinList([])
              setCurrentTab(tab.value)
            }}
            textStyle={{
              fontSize: '16px'
            }}
            wrapStyle={{
              bg: 'none'
            }}
          />
          <PoolsFilter
            isProfile={true}
            isPools={false}
            handleRefresh={() => {}}
            selectCoinList={selectCoinList}
            onClickSelectCoinList={onClickSelectCoinList}
            onDeleteSelectCoinList={onDeleteSelectCoinList}
          />
        </HStack>
        {!isApp && <Box h="1px" w="100%" bg="border" mt="-16px" />}
        {currentTab === 'positions' && <ProfilePosition showPosListGroupByPool={showPosListGroupByPool} />}
        {currentTab === 'vaults' && <ProfileVaults selectCoinList={selectCoinList} />}
      </VStack>
    </Block>
  )
}

export default ProfileLiquidity
