import { Block, CetusTooltip } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Center, HStack, Image, Skeleton, Text, VStack } from '@chakra-ui/react'
import HiddenDotted from '../HiddenDotted'
import { ProfileTabsProps } from './type'

function ProfileTab({ tabList, activeTab, tabData, onClickTab }: ProfileTabsProps) {
  const { isApp } = useWindowWidth()
  return (
    <HStack w="100%" flexWrap="wrap" mb={{ base: '12px', lg: '0px' }} mt={{ base: '0px', lg: '12px' }} gap={{ base: '8px', lg: '12px' }}>
      {tabList.map((item: any) => (
        <Block
          key={item.value}
          w={{ base: 'calc(50% - 4px)', lg: 'calc(25% - 9px)' }}
          p={{ base: '12px 8px', lg: '24px 16px' }}
          bgImage={activeTab === item.value ? 'url(/images/tab_active.png)' : 'none'}
          bgSize="100% 100%"
          borderRadius={{ base: '8px', lg: '16px' }}
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          cursor="pointer"
          bg="bg_secondary"
          backdropFilter="blur(20px)"
          borderColor={activeTab === item.value ? 'primary' : 'transparent'}
          onClick={() => onClickTab(item.value)}
          _hover={{
            bgImage: isApp ? 'none' : 'url(/images/tab_active.png)',
            bgSize: '100% 100%',
            borderColor: 'primary',
            img: {
              opacity: '0.8'
            }
          }}
        >
          <VStack align="flex-start" gap={{ base: '4px', lg: '10px' }}>
            <Text fontSize={{ base: '12px', lg: '14px' }} color="primary_gray">
              {item.title}
            </Text>
            <HStack h="22px">
              <HiddenDotted size="l">
                <CetusTooltip
                  placement="top"
                  tooltip={
                    <Text fontSize="12px" lineHeight="20px" color="text_caption">
                      {item.tooltip}
                    </Text>
                  }
                  showTooltip={item.showTooltip}
                >
                  <Center as="button">
                    <Skeleton isLoaded={!tabData[item.value]?.isLoading}>
                      <Text fontSize={{ base: '16px', lg: '20px' }} fontWeight="500" color="text_caption">
                        {tabData[item.value]?.totalValue}
                      </Text>
                    </Skeleton>
                  </Center>
                </CetusTooltip>
              </HiddenDotted>
            </HStack>
          </VStack>
          <Image
            w={{ base: '24px', lg: '38px' }}
            h={{ base: '24px', lg: '38px' }}
            src={item.activeImgUrl}
            opacity={activeTab === item.value ? '0.8' : '0.8'}
            position={{ base: 'fixed', lg: 'unset' }}
            right="8px"
          />
        </Block>
      ))}
    </HStack>
  )
}

export default ProfileTab
